import React, { ReactElement } from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"

function FourOhFour(): ReactElement {
  return (
    <Layout title="404 Not Found">
      <div className="bg-red-100 border border-red-600 rounded w-full">
        <h1 className="text-white bg-red-600 text-center py-4">Page Not Found</h1>
        <div className="p-4 text-center">
          <h3 className="text-lg font-bold">
            Oh no! It's a 404.
          </h3>
          <p className="text-gray-700 mt-4">
            Please try the links in the navigation menu or return <Link to="/" className="text-blue-600">home</Link>.
          </p>
        </div>
      </div>
    </Layout>
  )
}

export default FourOhFour
